:root {
  --font-barlow: "Barlow", sans-serif;
  --font-fraunces: "Fraunces", sans-serif;
  font-size: 18px;
}

:root {
  --soft-red: hsl(7, 99%, 70%);
  --yellow: hsl(51, 100%, 49%);
  --dark-desaturated-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --dark-moderate-cyan: hsl(168, 34%, 41%);
  --very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --very-dark-grayish-blue: hsl(213, 9%, 39%);
  --dark-grayish-blue: hsl(232, 10%, 55%);
  --grayish-blue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  overflow-x: hidden;
  font-family: var(--font-barlow);
  font-weight: 500;
  color: var(--very-dark-grayish-blue);
}

section.about .text, section.services .text {
  margin: auto;
  max-width: calc(500px + 14rem);
}
section.about .text p, section.services .text p {
  color: var(--dark-greyish-blue);
  line-height: 26px;
  padding-bottom: 2rem;
}
section.about .text h2, section.services .text h2 {
  font-family: var(--font-fraunces);
  color: var(--very-dark-desaturated-blue);
  line-height: 3.2rem;
  font-size: 3rem;
  padding-bottom: 2rem;
}
section.about .text a, section.services .text a {
  font-family: var(--font-fraunces);
  color: var(--very-dark-desaturated-blue);
  text-transform: uppercase;
  letter-spacing: 0.04rem;
  font-weight: 900;
  font-size: 15px;
}
section.about .text a:hover::before, section.services .text a:hover::before {
  content: "";
  position: absolute;
  margin-left: -8px;
  margin-top: 12px;
  height: 10px;
  width: 130px;
  background-color: var(--yellow);
  border-radius: 12px;
  z-index: -1;
}

section.services .text {
  text-align: center;
  padding: 30rem 7rem 1rem;
}
section.services .text h2 {
  font-size: 1.8rem;
  line-height: 2rem;
  padding-bottom: 1.4rem;
}
section.services .text p {
  max-width: 350px;
  margin: auto;
  font-size: 15px;
}

section.testimonials h3 {
  text-transform: uppercase;
  font-family: var(--font-fraunces);
  color: var(--grayish-blue);
  letter-spacing: 0.3rem;
}
section.testimonials p {
  line-height: 26px;
}
section.testimonials p.client-name {
  font-family: var(--font-fraunces);
  font-weight: 900;
  color: var(--very-dark-desaturated-blue);
  margin-top: 3.4rem;
}
section.testimonials p small {
  color: var(--grayish-blue);
}

main {
  display: grid;
}
main section.about,
main section.services {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
main section.about div,
main section.services div {
  min-height: 350px;
}
main section#first .cover {
  background: url("/images/desktop/image-transform.jpg") no-repeat center center/cover;
  grid-column: 2;
  grid-row: 1;
}
main section#second .cover {
  background: url("/images/desktop/image-stand-out.jpg") no-repeat bottom center/cover;
}
main section#second a:hover::before {
  background-color: var(--soft-red);
}
main section.services .cover:first-child {
  background: url("/images/desktop/image-graphic-design.jpg") no-repeat center center/cover;
}
main section.services .cover:first-child h2, main section.services .cover:first-child p {
  color: #25564b;
}
main section.services .cover:last-child {
  background: url("/images/desktop/image-photography.jpg") no-repeat center center/cover;
}
main section.services .cover:last-child h2, main section.services .cover:last-child p {
  color: #19536b;
}
main section.testimonials {
  padding: 125px;
  text-align: center;
}
main section.testimonials .testimonials-container {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
main section.testimonials .testimonials-container div {
  max-width: 310px;
  margin: auto;
  padding-top: 50px;
}
main section.testimonials img {
  border-radius: 50%;
  width: 80px;
  margin-bottom: 60px;
}
main section.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  background-color: #333333;
}
main section.gallery img {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 1441px) {
  main section.about,
main section.services {
    min-height: 80vh;
  }
}
@media screen and (max-width: 900px) {
  header h1 {
    font-size: 3rem;
  }

  main section.about,
main section.services {
    grid-template-columns: repeat(auto-fill, minmax(calc(300px + 3rem), 1fr));
  }
  main section.about .text,
main section.services .text {
    padding: 3rem;
  }
  main section.about .text h2,
main section.services .text h2 {
    line-height: normal;
    font-size: 2rem;
  }
  main section.services .text {
    padding: 16rem 3rem 1rem;
  }
  main section.testimonials {
    padding: 4rem 3rem;
  }
  main section.gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 696px) {
  header h1 {
    font-size: 2.3rem;
    padding: 1rem;
    letter-spacing: 0.8rem;
  }
  header a {
    color: var(--dark-grayish-blue);
  }

  nav {
    padding: 40px 1.3rem 80px;
  }
  nav.mobile-menu::after {
    display: block;
  }
  nav ul {
    display: none;
  }

  main section.about,
main section.services {
    width: 100vw;
  }
  main section.about .text,
main section.services .text {
    text-align: center;
  }
  main section.about .cover {
    grid-column: 1;
  }
  main section.testimonials {
    padding: 4rem 1rem;
  }
  main section.testimonials p.client-name {
    margin-top: 2.2rem;
  }
}
footer {
  background-color: #91d4c6;
  text-align: center;
  padding: 65px;
  color: var(--dark-moderate-cyan);
}
footer nav {
  display: flex;
  justify-content: center;
}
footer .social-media {
  display: flex;
  justify-content: center;
  gap: 20px;
}
footer .social-media svg {
  fill: #2d7668;
}
footer .social-media svg:hover {
  fill: var(--white);
}
footer a:hover {
  color: var(--white);
}

.attribution {
  bottom: 0;
  text-align: center;
  height: 20px;
  font-size: 14px;
}

header {
  width: 100vw;
  height: 100vh;
  background: url("/images/desktop/image-header.jpg") no-repeat bottom center/cover;
}
header h1 {
  text-transform: uppercase;
  font-family: var(--font-fraunces);
  font-weight: 900;
  color: var(--white);
  text-align: center;
  font-size: 4rem;
  letter-spacing: 1rem;
  padding: 80px;
}
header div img {
  margin-left: calc(50% - 18px);
}
header a {
  color: var(--white);
}
header li:last-child a {
  background-color: var(--white);
  font-family: var(--font-fraunces);
  color: var(--very-dark-desaturated-blue);
  text-transform: uppercase;
  font-size: inherit;
  padding: 15px 30px 15px 30px;
  border-radius: 35px;
}
header li:last-child a:hover {
  background-color: #70cfff;
  color: var(--white);
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 50px 80px;
}
nav ul {
  display: flex;
  gap: 40px;
}
nav ul li {
  list-style: none;
}
nav.mobile-menu .burger-btn::after {
  content: url('data:image/svg+xml, %3Csvg width="24" height="18" xmlns="http://www.w3.org/2000/svg"%3E%3Cpath d="M24 16v2H0v-2h24zm0-8v2H0V8h24zm0-8v2H0V0h24z" fill="white" fill-rule="evenodd"/%3E%3C/svg%3E');
}
nav.mobile-menu ul {
  position: absolute;
  top: 150px;
  right: 20px;
  display: none;
  flex-direction: column;
  background-color: var(--white);
  width: calc(100% - 2.6rem);
  font-size: 2rem;
  gap: 40px;
  padding: 80px;
  align-items: center;
}
nav.mobile-menu ul::before {
  content: "";
  position: absolute;
  top: -49px;
  right: 0;
  width: 0px;
  height: 0px;
  border-right: 50px solid var(--white);
  border-top: 50px solid transparent;
}
nav.mobile-menu ul a {
  color: var(--very-dark-desaturated-blue);
}
nav.mobile-menu ul li:last-child {
  margin-top: 20px;
}
nav.mobile-menu ul li:last-child a {
  font-family: var(--font-fraunces);
  background-color: var(--yellow);
  color: var(--very-dark-desaturated-blue);
  padding: 20px 40px;
  border-radius: 50px;
  text-transform: uppercase;
  font-size: 1.5rem;
}